}

.portfolio__footer {
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    align-items: flex-start;
    gap: 10px;

    margin: 12vh 15vw;

    border-radius: 10px;
}

.portfolio__footer-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
}

.portfolio__footer-text h1 {
    text-align: left;
    font-family: 'Montserrat';
    font-style: italic;
    font-weight: 900;
    font-size: 40px;
    color: white;
    text-shadow:
    -2px -2px 0 #000,
     0   -2px 0 #000,
     2px -2px 0 #000,
     2px  0   0 #000,
     2px  2px 0 #000,
     0    2px 0 #000,
    -2px  2px 0 #000,
    -2px  0   0 #000;

}

.portfolio__footer-text p {
    text-align: left;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;

    max-width: 500px;

    color: #002445;
}

.portfolio__form {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 0px 20px;
    gap: 25px;
}

.portfolio__footer form input[type=text], .portfolio__footer textarea {
    all:unset;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 15px 25px;
    gap: 10px;
    background: #F1F1F1;
    border-radius: 15px;

    text-align: left;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height */

    width: 500px;

    color: rgba(0, 0, 0, 0.5);
}

.portfolio__footer form input[type=submit] {
    all:unset;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 15px 50px 15px;
    gap: 10px;

    font-family: 'Montserrat';
    font-style: italic;
    font-weight: 900;
    font-size: 24px;
    color: #87CEEB;
    text-shadow:
    -2px -2px 0 #000,
     0   -2px 0 #000,
     2px -2px 0 #000,
     2px  0   0 #000,
     2px  2px 0 #000,
     0    2px 0 #000,
    -2px  2px 0 #000,
    -2px  0   0 #000;
    background-color: #87CEEB;

    border-radius: 100px;

    cursor: pointer;

}

@media screen and (max-width: 1600px) {
    .portfolio__footer {
        margin: 10% 10%;
    }
}

@media screen and (max-width: 1300px) {
    .portfolio__footer form input[type=text], .portfolio__footer textarea {
        width: 350px;
    }

    .portfolio__footer-text p {
        max-width: initial;
    }
}

@media screen and (max-width: 1180px) {
    .portfolio__footer form input[type=text], .portfolio__footer textarea {
        width: 400px;
    }
    .portfolio__footer {
        margin: 15% 2%;
    }
}

@media screen and (max-width: 780px) {
    .portfolio__footer {
        flex-direction: column;
        margin: 15% 5%;
    }

    .portfolio__footer-text {
        max-width: 500px;
    }

    .portfolio__form {
        padding: 0;
    }

    .portfolio__footer form input[type=text], .portfolio__footer textarea {
        margin-left: 0;
        width: 82vw;
    }
}

@media screen and (max-width: 500px) {
    .portfolio__footer {
        margin: 20% 5%;
    }
    .portfolio__footer h1 {
        font-size: 2rem;
    }
    .portfolio__footer p {
        font-size: 1rem;
    }

    .portfolio__footer form input[type=submit] {
        font-size: 1rem;
    }

    .portfolio__footer form input[type=text], .portfolio__footer textarea {
        width: 80vw;
    }
}

@media screen and (max-width: 400px) {
    .portfolio__footer form input[type=text], .portfolio__footer textarea {
        width: 75vw;
    }
    
}